<script>
  import { LazyLoadContainer, LazyLoad } from 'svelte-lazyload';
  import { onMount } from "svelte";
  let data = [];
  onMount(async function() {
    const response = await fetch("./Icons.json");
    const json = await response.json();
    data = json;
  });
</script>

<LazyLoadContainer threshold="0" rootMargin='3000px'>
{#each data as link, i}
<div class="icon-container">
 <LazyLoad id="{i}" placeholder="⌛">
  <a href="/mdicons/icons/{link}.svg" download>
    <img src="/mdicons/icons/{link}.svg" alt="{link}">
  </a>
  </LazyLoad>
</div>
{/each}
</LazyLoadContainer>

<style>
img{
  min-height: 48px;
  /* min-width: 48px; */
}
.icon-container{
  min-height: 48px;
  min-width: 48px;
  display: inline-block;
  float: left;
  padding: 0 calc((100vw - (32px + 144px)) / 6);
}
:global(.placeholder){
  margin: 8px 14px;
  position: absolute;min-height: 48px;
  min-width: 48px;
}
</style>

